@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.homepage{
    display: grid;
    grid-template-columns: 40% 60%;
    padding: 1em;
    grid-column-gap:1em;
    grid-row-gap: 1.5em;
    grid-auto-rows: minmax(300px, auto);
    grid-auto-columns: minmax(300px, auto);
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
}

.homepage > div{
    padding: 20px;
    font-size: 30px;
    text-align: center;
    border-radius: 10px;
}
.Profile{
    background-color: lightblue;
    border: 1px solid rgba(0, 0, 0, 0.8);
}
.StartButton{
    width: 400px;
    height: 100px;
    background: #f3f0f1;
    position: relative;
    background: #f3f0f1;
    border-radius: 32px;
    text-align: center;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
    margin-left: auto;
    margin-right: auto;
}

.StartButton-text {
    line-height: 100px;
    font-family: "Montserrat", sans-serif;
    font-size: 32px;
    font-weight: semibold;
  }

.StartButton {
    box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8), 6px 6px 10px rgba(0, 0, 0, 0.2);
    color: #6f6cde;
  }
.StartButton:hover {
    opacity: 0.3;
    box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8), 6px 6px 10px rgba(0, 0, 0, 0.2);
  }
.StartButton:active {
    opacity: 1;
    box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5), inset 8px 8px 16px rgba(0, 0, 0, 0.1);
    color: #79e3b6;
  }



